<template>
    <div class='rounded-md w-full h-64 absolute overflow-hidden'>
        <transition :name='currentSlide.direction'>
            <div v-if='currentSlide.slide === index' ref='slide' class='w-full h-full flex items-center'>
                <slot></slot>
            </div>
        </transition>
    </div>
</template>

<style scoped>
    .slide-left-enter-active {
        animation: slide-left-in 0.25s ease-in;
    }
    .slide-left-leave-active {
        animation: slide-left-out 0.25s ease-in;
    }
    @keyframes slide-left-in{
        from  { transform: translateX(-100%);}
        to { transform: translateX(0);} 
    }
    @keyframes slide-left-out{
        from  { transform: translateX(0%);}
        to { transform: translateX(100%);} 
    }
    
    .slide-right-enter-active {
        animation: slide-right-in 0.25s ease-in;
    }
    .slide-right-leave-active {
        animation: slide-right-out 0.25s ease-in;
    }
    @keyframes slide-right-out{
        from  { transform: translateX(0%);}
        to { transform: translateX(-100%);} 
    }
    @keyframes slide-right-in{
        from  { transform: translateX(100%);}
        to { transform: translateX(0);} 
    }
</style>

<script>
import { defineComponent, ref, watch } from 'vue'

export default defineComponent({
    props: {
        currentSlide: Object,
        index: Number
    },
    setup(props) {
        const slide = ref(null)
        watch(props.currentSlide, (newValue) => {
            if( slide.value ) {
                if(newValue.direction == 'increased')
                    console.log(slide.value.getAttribute('data-aos'))
                if(newValue.direction == 'decreased')    
                    console.log(newValue, slide.value)
            }
            console.log(props.currentSlide.direction)
        })
        console.log(props)
        return {
            slide
        }
    },
    mounted() {
        console.log()
    }
})
</script>
