<template>
    <div :class='{ zindex: toggleInfo }' class='fixed top-0 w-full h-full flex justify-center items-center'>
        <transition appear enter-active-class="fade-in" leave-active-class="fade-out">
            <div v-if='toggleInfo' class='bg-black opacity-50 fade-in w-full h-full'></div>
        </transition>
        <transition enter-active-class="animated up" leave-active-class="animated down">
            <div v-if='toggleInfo' class='bg-white size rounded-md absolute'>
                <div class='absolute w-full z-10 flex justify-end'>
                    <button @click="emit('toggle')" class='w-10 h-10 text-2xl'>X</button>
                </div>
                <div class='rounded-md w-full h-64 flex justify-center items-center'>
                    <div class='h-full w-full flex justify-center items-end'>
                        <Carousel #default='{ currentSlide }'>
                            <CarouselItem 
                            v-for='(item, index) in details.images' 
                            :key='item' 
                            :index='index' 
                            :currentSlide='currentSlide'>
                                <img alt='img' :src='item'>
                            </CarouselItem>
                        </Carousel>
                        <div class='absolute w-full'>
                            <button @click="openApp()" class='bg-blue-400 m-2 text-white px-3 py-2'>Open App</button>
                            <button class='bg-yellow-600 m-2 text-white px-3 py-2'>Source Code</button>
                        </div>
                    </div>
                </div>
                <div class='p-5'>
                    <h1 class='text-2xl'>{{ details.name }}</h1>
                    <h1 class='text-lg'>{{ details.description }}</h1>
                    <div v-html='details.content'></div>
                </div>
            </div>
        </transition>
    </div>
</template>

<style scoped>
    .size {
        width: 300px;
        height: 500px;
    }
    @media (min-width: 768px) {
        .size {
            width: 500px;
            height: 500px;
        }
    }
    .zindex {
        z-index: 20;
    }
    .up {
        animation: fadeInUp;
        animation-duration: 500ms;

    }
    .down {
        animation: fadeOutUp;
        animation-duration: 500ms;

    }
    .fade-in {
        animation: fadeIn 0.5s;
        -webkit-animation: fadeIn 0.5s;
        -moz-animation: fadeIn 0.5s;
        -o-animation: fadeIn 0.5s;
        -ms-animation: fadeIn 0.5s;
}
    .fade-out {
        animation: fadeIn 0.5s;
        -webkit-animation: fadeIn reverse 0.5s;
        -moz-animation: fadeIn reverse 0.5s;
        -o-animation: fadeIn reverse 0.5s;
        -ms-animation: fadeIn reverse 0.5s;
}
    @keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:0.5;}
    }

    @-moz-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:0.5;}
    }

    @-webkit-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:0.5;}
    }

    @-o-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:0.5;}
    }

    @-ms-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:0.5;}
    }
</style>

<script>
import { defineComponent } from 'vue'
import Carousel from '@/components/Carousel.vue'
import CarouselItem from '@/components/CarouselItem.vue'

export default defineComponent({
    props: {
        details: Object,
        toggleInfo: Boolean,
    },
    components: {
        Carousel,
        CarouselItem
    },
    setup(props, { emit }) {
        const openApp = () => {
            window.open(props.details.appLink)
        }
        return {
            emit,
            openApp
        }
    },
    data() {
        return {
            
        }
    },
    methods: {
        log() {
            console.log(this)
        }
    }
})
</script>
