<template>
    <div data-aos="fade-up" class='bg-white rounded-xl shadow-2xl w-80 z-10 m-5'>
        <img class='rounded-xl' :src='image'>
        <div class='p-3'>
            <h1 class='font-bold text-3xl'>{{ name }}</h1>
        </div>
        <div class='p-3 flex justify-center'>
            <button class='mx-2 bg-blue-400 text-white px-4 py-2 rounded-md shadow-2xl'>Open App</button>
            <button @click='openInfo()' class='mx-2 border border-black px-4 py-2 rounded-md shadow-2xl'>View Details</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from 'vue'

export default defineComponent({
    props: {
        name: String,
        image: String
    },
    setup(_, { emit }) {
        const openInfo = () => {
            emit('open')
        } 
        return {
            openInfo
        }
    }
})
</script>
