<template>
    <div class='w-full h-full flex items-center'>
        <div ref='children' class='h-full w-full'>
            <slot :currentSlide='currentSlide'></slot>
        </div>
        <div class='absolute w-full flex justify-between'>
            <button @click='prev()' class='text-7xl z-10 w-20 h-24'>&lt;</button>
            <button @click='next()' class='text-7xl z-10 w-20 h-24'>></button>
        </div>
    </div>
</template>

<script>
import { defineComponent, ref, onMounted } from 'vue'

export default defineComponent({
    components: {
    },
    props: {
    },
    setup() {
        const children = ref(null)
        const slides = ref([])
        const currentSlide = ref({slide: 1, direction: 'default'})
        const next = () => {
            currentSlide.value.slide++
            currentSlide.value.direction = 'slide-right'
            currentSlide.value.slide = currentSlide.value.slide >= children.value.children.length ? 0 : currentSlide.value.slide
            console.log(currentSlide.value.slide, children.value.children.length)
        }
        const prev = () => {
            currentSlide.value.slide--
            currentSlide.value.direction = 'slide-left'
            currentSlide.value.slide = currentSlide.value.slide < 0 ? children.value.children.length-1 : currentSlide.value.slide
            console.log(currentSlide.value.slide, children.value.children.length)
        }
        onMounted(() => {
            let values = children.value.children
            for(let child of values) {
                if(child.nodeName == "#comment")
                    slides.value.push(child)
                console.log(children.value.children)
            }
            currentSlide.value.slide = 0
            console.log(children.value.children.length)
        })
        return {
            children,
            currentSlide,
            next,
            prev
        }
    },
    mounted() {
        console.log()
    } 
})
</script>
